@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  background-color: rgb(11, 5, 41);
  overflow-x: hidden;
}

.main-div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 1rem 3rem;
  background-color: white;
  border-radius: 0.5rem;
}

textarea {
  width: 100%;
  height: 30vh;
  resize: none;
  border: 2px solid rgba(14, 3, 26, 0.651);
  border-radius: 0.2rem;
  color:  rgb(11, 5, 41);
  padding: 1rem;
  font-weight: 600;
}

/* button styling  */

.btns {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  flex-wrap: wrap;
}

button {
  outline: none;
  border: none;
  width: 15rem;
  height: 3rem;
  margin: 1rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.418);
  background-color: white;
  border-radius: 0.5rem;
  font-weight: 700;
  letter-spacing: 1px;
  cursor: pointer;
  transition: 0.2s all ease-in;
}
button:hover{
  background-color: rgb(11, 5, 41);
  color: white;
}




.text-info{
  color: white;
  padding: 4rem;
  font-size: 1.8rem;
}

.infobox{
  display: flex;
  align-items: center;
  margin-top: 2rem;
  
}
.infobox p{
  margin-right: 2rem;
  border: 2px solid white;
  padding: 1rem 2rem; 
  border-radius: .2rem;
  font-size: 1.5rem;
}

/* ========================================================== */
@media screen and (max-width:900px) {
  
